import {createElement} from '../../render.js';

function createTemplate(title, price, date, datetime = '01-01-1970', isFavourite = false) {
  const isFavouriteButton = isFavourite ? 'event__favorite-btn--active' : '';
  return (
    `<li class="trip-events__item">
      <div class="event">
        <time class="event__date" datetime="${datetime}">${date}</time>
        <div class="event__type">
          <img class="event__type-icon" width="42" height="42" src="img/icons/sightseeing.png" alt="Event type icon">
        </div>
        <h3 class="event__title">${title}</h3>
        <div class="event__schedule">
          <p class="event__time">
            <time class="event__start-time" datetime="${datetime}T11:15">11:15</time>
            &mdash;
            <time class="event__end-time" datetime="${datetime}T12:15">12:15</time>
          </p>
          <p class="event__duration">01H 00M</p>
        </div>
        <p class="event__price">
          &euro;&nbsp;<span class="event__price-value">${price}</span>
        </p>
        <button class="event__favorite-btn ${isFavouriteButton}" type="button">
          <span class="visually-hidden">Add to favorite</span>
          <svg class="event__favorite-icon" width="28" height="28" viewBox="0 0 28 28">
            <path d="M14 21l-8.22899 4.3262 1.57159-9.1631L.685209 9.67376 9.8855 8.33688 14 0l4.1145 8.33688 9.2003 1.33688-6.6574 6.48934 1.5716 9.1631L14 21z"/>
          </svg>
        </button>
        <button class="event__rollup-btn" type="button">
          <span class="visually-hidden">Open event</span>
        </button>
      </div>
    </li>`
  );
}

export default class PointItem {
  constructor(title, price, date, datetime = '01-01-1970', isFavourite = false) {
    this.title = title;
    this.price = price;
    this.isFavourite = isFavourite;
    this.date = date;
    this.datetime = datetime;
  }

  getTemplate() {
    return createTemplate(this.title, this.price, this.date, this.datetime, this.isFavourite);
  }

  getElement() {
    if (!this.element) {
      this.element = createElement(this.getTemplate());
    }

    return this.element;
  }

  removeElement() {
    this.element = null;
  }
}
